import React, { Component } from "react";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import axios from "axios";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;

class FormCiok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      kodeBarang: [],
      kodeJenis: "",
      namaJenis: "",
      kadarCetak: "",
      kadarBeli: "",
      columns: [
        {
          dataField: "kodeBarang",
          text: "Kode Barang",
        },
        {
          dataField: "bruto",
          text: "Bruto",
        },
        {
          dataField: "kadarTransaksi",
          text: "Harga (%)",
        },
        {
          dataField: "netto",
          text: "Netto(Gr)",
        },
      ],
      tampungCiok: [],
    };
  }

  componentDidMount() {
    axios
      .get(server + "subcategorys")
      .then((res) =>
        this.setState({
          kodeBarang: res.data,
        })
      )
      .catch((err) => console.log(err));
  }
  pilihBarang = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      kadarCetak: result_explode[1],
      kadarBeli: result_explode[2],
    });
  };

  onclick1() {
    this.props.change("netto", this.props.netto);
  }
  setKadar() {
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarBeli);
  }

  render() {
    var filtered = this.state.kodeBarang.filter((d) => d.kodeKategori === "CK");
    return (
      <div className="col-lg-12">
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-lg-4 mb-3">
              <label htmlFor="">Kode Barang</label>
              <Field
                name="kodeBarang"
                className="form-control"
                component="select"
                onChange={this.pilihBarang()}
                onBlur={this.setKadar()}
              >
                <option
                  value="DEFAULT | DEFAULT | DEFAULT | DEFAULT"
                  key="DEFAULTKODEBARANG1"
                ></option>
                {filtered.map((list) => (
                  <option
                    value={
                      list.kodeJenis +
                      "|" +
                      list.kadarCetak +
                      "|" +
                      list.kadarBeli
                    }
                    key={list.kodeJenis}
                  >
                    {list.namaJenis}
                  </option>
                ))}
              </Field>
            </div>
            <div className="col-lg-8"></div>
            <div className="col-md-3 form-group">
              <Field
                name="kadar"
                label="Kadar (%)"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 form-group">
              <Field
                name="bruto"
                label="Berat (Gr)"
                type="number"
                component={renderField}
                onBlur={() => this.onclick1()}
              />
            </div>
            <div className="col-md-3 form-group">
              <Field
                name="kadarTransaksi"
                label="Harga (%)"
                type="number"
                component={renderField}
                onBlur={() => this.onclick1()}
              />
            </div>
            <div className="col-md-3 form-group">
              <Field
                name="netto"
                label="Murni (Gr)"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button
                  className="btn btn-primary"
                  type="submit"
                  disabled={this.props.submitting}
                >
                  Simpan
                </button>
              </div>
            </div>
            {this.props.data ? (
              <ToolkitProvider
                keyField="id"
                data={this.props.data}
                columns={this.state.columns}
                search
              >
                {(props) => (
                  <div className="form-group">
                    <BootstrapTable
                      {...props.baseProps}
                      striped
                      hover
                      condensed
                      bordered={true}
                      headerClasses="header-table"
                      pagination={paginationFactory()}
                    />
                  </div>
                )}
              </ToolkitProvider>
            ) : null}
          </div>
        </form>
      </div>
    );
  }
}

FormCiok = reduxForm({
  form: "FormCiok",
  enableReinitialize: true,
})(FormCiok);
const selector = formValueSelector("FormCiok"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi } = selector(state, "bruto", "kadarTransaksi");
  return {
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
  };
})(FormCiok);
